.parallax {
  text-align: center;
  background-image: url("http://localhost:3000/fullgym.jpg");
  /* Full height */
  height: 100vh;
  width: 100%;
  /* Create the parallax scrolling effect */
  opacity: 0.8;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  -webkit-filter: blur(6px);
  -moz-filter: blur(6px);
  -o-filter: blur(6px);
  -ms-filter: blur(6px);
  filter: blur(6px);
}
.home_container{
  display: inline-flex;
  flex-direction: column;
  top: 0px;
  position: absolute;
  width: 100%;
  height: 100%;
}
.conteudo{
  margin: 30vh auto;
  padding: 2em;
  background-color: white;
  border: solid 0.5em white;
  border-radius: 2em;
  text-align: center;
}
.shadow{
  background-color: black;
}
