.sidebar{
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  z-index: 100;
  padding: 0;
  box-shadow: inset -1px 0 0 rgba(0, 0, 0, .1);
}

#root{
  height: 100vh;
}
.cabecalho{
  height: 5vh;
}
.corpo{
  height: 100vh;
  overflow: hidden;
  background: #cecece;
  background-image: url("http://localhost:3000/fullgym.jpg");
  height: 100vh;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.exercicio-container{
  height: 80vh;
  overflow-y: scroll;
  background: #fefefe;
  padding: 1em;
  border: solid 0.3 #cecece ;
  border-radius: 0.5em;
}
.exercicio-item{
  flex: 0;
  border-top: solid 1px #cecece;
  border-bottom: solid 1px #cecece;
  width: calc(100%);
}
.exercicio-item:hover{
  width: calc(100% - 0.25em);
  border-left: solid 0.25em #fbff00;
  margin-left: 0.25em;
}

.modal-dialog{
  min-width: 80vw;
}

.modal-image{
  min-width: 20vw;
  height:auto;

}

.color-scheme{
  background-color: black;
  color: yellow;
  text-transform: uppercase;
}

.btn-color-scheme{
  background-color: black;
  color: yellow;
}
.logo{
  display: inline-block;
  max-height: 80%;
  width: 20%;
}
